 <template>

    <div id="ContentZone" ref="root">
        <div id="LeftTagZone">
            <input type="button" 
                v-for=" (tagText,index) in TagTexts " 
                :value="tagText" 
                :key="index" 
                :class="LeftTag[index]" 
                @mouseover="MouseOverTag(index)" 
                @mouseout="MouseOutTag(index)" 
                @click="ClickedTag(index)"
                />
            <input type="button" :value="ReturnButtonText" @click="Return">
        </div>

        <div v-cloak id="ContentArea" :class="ContentAreaDivStyle">
            <h1 :class="TitleLabelStyle"> {{TitleLabelText}} </h1>
            <transition name="ContentArea" appear>
                <textarea :class="ContentAreaStyle" v-if="IfShowContentArea" v-model="Content"></textarea>
            </transition>
        </div>
    </div>
    
 </template>



<script>

const DefualtTitleLabelText = "请点击左边的标签选择主题";

export default {

    name: "ContentZone",

    props:{
        authorInfo: {
            type: String,
            default: "LabelText",
        },
    },

    data(){
        return {
            TagTexts:[
                "学习HTML",
                "学习JavaScript",
                "学习Node.js",
                "学习Vue",
            ],
            LeftTag:[
                {
                    HasBeenClicked: false,
                    LeftTagZone_Tag_Mouse_Out: true,
                    LeftTagZone_Tag_Mouse_Over: false,
                    LeftTagZone_Tag_Mouse_Clicked: false,
                    LeftTagZone_Tag_Mouse_Out_Clicked: false,
                    LeftTagZone_Tag_Mouse_Over_Clicked: false,
                },
                {
                    HasBeenClicked: false,
                    LeftTagZone_Tag_Mouse_Out: true,
                    LeftTagZone_Tag_Mouse_Over: false,
                    LeftTagZone_Tag_Mouse_Clicked: false,
                    LeftTagZone_Tag_Mouse_Out_Clicked: false,
                    LeftTagZone_Tag_Mouse_Over_Clicked: false,
                },
                {
                    HasBeenClicked: false,
                    LeftTagZone_Tag_Mouse_Out: true,
                    LeftTagZone_Tag_Mouse_Over: false,
                    LeftTagZone_Tag_Mouse_Clicked: false,
                    LeftTagZone_Tag_Mouse_Out_Clicked: false,
                    LeftTagZone_Tag_Mouse_Over_Clicked: false,
                },
                {
                    HasBeenClicked: false,
                    LeftTagZone_Tag_Mouse_Out: true,
                    LeftTagZone_Tag_Mouse_Over: false,
                    LeftTagZone_Tag_Mouse_Clicked: false,
                    LeftTagZone_Tag_Mouse_Out_Clicked: false,
                    LeftTagZone_Tag_Mouse_Over_Clicked: false,
                },
            ],

            ContentAreaDivStyle:{
                ContentAreaDIV: true
            },

            TitleLabelStyle:{
                ContentArea_TitleLabel: true
            },

            TitleLabelText: DefualtTitleLabelText,

            ContentAreaStyle:{
                ContentArea : true
            },
            
            Content: "Test Content",

            IfShowContentArea: false,

            ReturnButtonText: "返回上一级",
        }
    },

    methods:{
        MouseOverTag(tagId){
            var o_thisTag = this.LeftTag[tagId];
            if(o_thisTag.HasBeenClicked){
                o_thisTag.LeftTagZone_Tag_Mouse_Over_Clicked = true;
                o_thisTag.LeftTagZone_Tag_Mouse_Out_Clicked = false;
            }else{
                o_thisTag.LeftTagZone_Tag_Mouse_Over = true;
                o_thisTag.LeftTagZone_Tag_Mouse_Out = false;
            }
        },
        
        MouseOutTag(tagId){
            var o_thisTag = this.LeftTag[tagId];
            if(o_thisTag.HasBeenClicked){
                o_thisTag.LeftTagZone_Tag_Mouse_Over_Clicked = false;
                o_thisTag.LeftTagZone_Tag_Mouse_Out_Clicked = true;
            }else{
                o_thisTag.LeftTagZone_Tag_Mouse_Over = false;
                o_thisTag.LeftTagZone_Tag_Mouse_Out = true;
            }
        },

        ResetDefaultStyle(o_tag){
            o_tag.LeftTagZone_Tag_Mouse_Out = true;
            o_tag.LeftTagZone_Tag_Mouse_Over = false;
            o_tag.LeftTagZone_Tag_Mouse_Clicked = false;
            o_tag.LeftTagZone_Tag_Mouse_Out_Clicked = false;
            o_tag.LeftTagZone_Tag_Mouse_Over_Clicked = false;
        },

        ClickedTag(tagId){
            this.LeftTag.forEach(o_tag => {
                o_tag.HasBeenClicked = false;
                this.ResetDefaultStyle(o_tag);
            });
            var o_thisTag = this.LeftTag[tagId];
            o_thisTag.HasBeenClicked = true;
            o_thisTag.LeftTagZone_Tag_Mouse_Out = false;
            o_thisTag.LeftTagZone_Tag_Mouse_Clicked = true;

            this.TitleLabelText = this.TagTexts[tagId];

            this.IfShowContentArea = true;
        },

        Return(){
            this.LeftTag.forEach(o_tag => {
                o_tag.HasBeenClicked = false;
                this.ResetDefaultStyle(o_tag);
            });
            this.TitleLabelText = DefualtTitleLabelText;
            this.IfShowContentArea = false;
        }
    }

}

</script>


<style lang="scss" scoped>

#ContentZone{
    clear: both;
}

#LeftTagZone{
    height: 400px;
    width: 200px;
    float: left;
    Top: 30px;
    left: 5px;
    position: relative;
}

.LeftTagZone_Tag_Base{
    -webkit-appearance:button;
    font-style: oblique;
    font-family: Arial, Helvetica, sans-serif;
    outline: none;
    margin-bottom: 20px;
}

$mouseAnimTime :  all 0.5s ease-in-out;

.LeftTagZone_Tag_Mouse_Out{
    @extend .LeftTagZone_Tag_Base;
    height: 30px;
    width: 150px;
    border: 5px blue solid;
    color: black;
    font-size: 5px;
    transition: $mouseAnimTime;
}

.LeftTagZone_Tag_Mouse_Over{
    @extend .LeftTagZone_Tag_Base;
    height: 50px;
    width: 200px;
    border: 10px yellowgreen solid;
    color: red;
    font-size: 10px;
    transition: $mouseAnimTime;
}

.LeftTagZone_Tag_Mouse_Out_Clicked{
    @extend .LeftTagZone_Tag_Base;
    height: 80px;
    width: 180px;
    border: 8px rgb(182, 28, 136) double;
    color: rgb(121, 163, 5);
    font-size: 7px;
    transition: $mouseAnimTime;
}

.LeftTagZone_Tag_Mouse_Over_Clicked{
    @extend .LeftTagZone_Tag_Base;
    height: 80px;
    width: 230px;
    border: 12px rgb(182, 28, 136) double;
    color: rgb(121, 163, 5);
    font-size: 12px;
    transition: $mouseAnimTime;
}

.LeftTagZone_Tag_Mouse_Clicked{
    @extend .LeftTagZone_Tag_Base;
    height: 80px;
    width: 230px;
    border: 12px rgb(182, 28, 136) double;
    color: rgb(121, 163, 5);
    font-size: 12px;
}

.ContentAreaDIV{
    height: auto;
    width: 700px;
    float: left;
    left: 50px;
    position: relative;
}

.ContentArea_TitleLabel{
    font-family: initial;
    font-style: oblique;
    font-size: 50px;
    font-weight: 900;
    color: #0004fc;
    text-align: center;
    margin: 0px;
    user-select: none;
}

.ContentArea{
    height: 900px;
    width: 100%;
    font-size: 25px;
    color: white;
    background: black;
}

.ContentArea-enter, .ContentArea-leave-to{
    width: 100%;
    height: 0px;
}

.ContentArea-enter-active, .ContentArea-leave-active{
    transition: all 0.5s ease-in-out;
}

</style>