<template>
    <div id="TitleZone" ref="root">  
        <br>
        <br>
        <v-touchable-label text="GSan的电子之海" ></v-touchable-label>
        <!-- 实现四个tag：主页，学习笔记，灌水，返回上级 -->
        <v-menu-bar ref="menuBar"></v-menu-bar>
    </div>
</template>

<script>

import VTouchableLabel from '../view/TouchableLabel.vue';
import VMenuBar from '../view/MenuBar/MenuBar.vue';

let MenuItem = VMenuBar.MenuItem;
let DropDownItem = VMenuBar.DropDownItem;

export default {
    name: "TitleZone",
    data(){
        return {
            titleZoneMinHeight : 10,
        }
    },
    components:{
        VTouchableLabel,
        VMenuBar,
    },

    mounted(){
        
        let menuBar = this.$refs.menuBar;

        menuBar.AddAnItem(new MenuItem("return", "返回上级", ()=>{ this.$emit("onReturn");}, null, true));

        menuBar.AddAnItem(new MenuItem("article", "随笔", ()=>{},
        [
            new DropDownItem(1, "成长历程", ()=>{ this.$emit("onArticle", 1); }),
            new DropDownItem(2, "个人编程哲学", ()=>{ this.$emit("onArticle", 2); })
        ]));

        menuBar.AddAnItem(new MenuItem("code", "编程学习", ()=>{},
        [
            new DropDownItem(1, "HTML", ()=>{ this.$emit("onCode", 1); }),
            new DropDownItem(2, "CSS", ()=>{ this.$emit("onCode", 2); }),
            new DropDownItem(3, "JavaScript", ()=>{ this.$emit("onCode", 3); }),
        ]));
        
        menuBar.AddAnItem(new MenuItem("home", "首页", ()=>{ this.$emit("onHome")}));
    },

    TitleZoneWindowEventCallback(e){
        VMenuBar.MenuBarWindowEventCallback(e);
    }
}

</script>


<style lang="scss" scoped>

#TitleZone{
    width: 100%;
    height: 160px;
    background-image:url("../../image/FGO.jpg");
    background-repeat: repeat;
    clip-path: margin-box;
}

</style>